@import url(https://fonts.googleapis.com/css?family=ABeeZee:400,400i&display=swap);
@import url(https://fonts.googleapis.com/css?family=ABeeZee:400,400i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  background: linear-gradient(128.03deg, #BB6BD9 13.39%, #483A4E 78.11%);
}

body {
  min-height: 100vh;
  /* width: 100vw; */
  font-size: 12px;
  overflow-y: scroll;
}

.info-card{background-color:white;height:13em;width:19.45em;margin-right:1px;box-shadow:0px 5px 10px rgba(0,0,0,0.15);display:flex;justify-content:center;align-items:center;flex-direction:column;font-family:'ABeeZee'}.info-card p{color:#828282;margin:0;letter-spacing:0.03em}.info-card img{margin-bottom:0.5em;width:auto;height:4em}.info-card .number{font-size:3.5em}.info-card .title{font-size:1em;text-transform:uppercase}.info-card:last-child{margin-right:0}.landing-page{display:flex;min-height:100vh;justify-content:flex-end;align-items:center;overflow-y:scroll}.landing-page .page-content{display:flex}.landing-page .page-content .left-content{display:flex;flex-direction:column;justify-content:center;align-items:center}.landing-page .page-content .left-content .content-title{letter-spacing:0.25em;text-transform:uppercase;color:white;font-size:1.6em;margin:0}.landing-page .page-content .left-content .landing-button{width:14.4em;background:#BB6BD9;border-radius:5.833em;border:0;margin-top:1em;box-shadow:0px 5px 10px rgba(0,0,0,0.15);font-size:2.167em;text-decoration:none;color:white;text-transform:uppercase;letter-spacing:0.25em;text-align:center;padding:1em 0}.landing-page .page-content .left-content .landing-button:hover{cursor:pointer}.landing-page .page-content .info-cards{display:flex;margin:4em 0}@media screen and (max-width: 1302px){.landing-page{justify-content:center}.landing-page .page-content .right-content{display:none}}@media screen and (max-width: 768px){.info-card{margin-right:0;margin-bottom:1px;width:90%}.info-card:last-child{margin-bottom:0}.info-cards{display:flex;width:100%;align-items:center}.landing-page{justify-content:center;margin-top:7em}.landing-page .page-content .left-content .content-title{text-align:center;font-size:1.2em;padding:0 0.4em}.landing-page .page-content .left-content .landing-button{width:90%;font-size:1.3em;border-radius:0.2em}.landing-page .page-content .left-content .landing-button:last-child{margin-bottom:7em}.landing-page .page-content .info-cards{flex-direction:column}.landing-page .page-content .right-content{display:none}}

.header{background-color:rgba(0,0,0,0.2);height:76px;display:flex;align-items:center;justify-content:space-around;font-family:'ABeeZee';font-size:1em;position:fixed;width:100%;top:0;z-index:3}.header img{width:auto;height:3.1666em}.header nav{align-self:center}.header nav ul{list-style:none}.header nav ul li{display:inline-block;text-transform:uppercase;margin-right:6em;color:white;letter-spacing:0.03em}.header nav ul li .active{color:#F2C94C}.header nav ul li a{text-decoration:none;color:white}.header nav ul li:last-child{margin-right:0}.header nav ul li:hover{cursor:pointer}.mobile-header{display:none}@media screen and (max-width: 1302px){.header{display:none;height:52px}.mobile-header{display:flex;justify-content:space-between;background-color:rgba(0,0,0,0.7)}.mobile-header .menu-icon{height:2em;width:auto;position:relative;left:1em}.mobile-header .menu-icon--close{position:absolute;left:auto;left:initial;right:1em;top:2em}.mobile-header .logo{height:2em;width:auto}.mobile-header .profile-image{height:2.5em;width:2.5em;background:red;position:relative;right:1em;border-radius:50%}.mobile-header .side-bar{position:fixed;height:100%;width:100%;background:#483A4E;top:0;-webkit-transition:left 500ms ease-in-out;transition:left 500ms ease-in-out}.mobile-header .side-bar nav ul{font-size:2em}.mobile-header .side-bar nav ul li{display:block;padding:1em 0 1em 1em}.mobile-header .side-bar nav:last-child{position:absolute;bottom:0}}

